.task-board-container {
  padding-top: 50px;
  height: 100vh;
  width: 100vw;

  .columns-container {
    min-height: calc(100vh - 100px);
    width: calc(100% - 100px);
    justify-content: center;
    flex-direction: row;
    column-gap: 40px;
    display: flex;
    margin: auto;

    .column-container {
      height: 100%;
      min-height: 100vh;
      width: 100%;
      max-width: 300px;
      background-color: #AAC;

      .title {
        display: grid;
        font-size: 20px;
        padding: 20px 10px;
        font-weight: bolder;
        grid-auto-flow: column;

        span {
          text-align: left;
          align-self: center; }

        .avatar {
          margin-left: auto; } } } }


  .button-new-card {
    position: fixed;
    bottom: 20px;
    right: 20px; } }

.new-card-container {
  .MuiBox-root {
    margin: 20px 0;
    margin-top: 2px;
    display: flex;
    align-items: flex-end;

    .MuiSvgIcon-root {
      color: rgba(0, 0, 0, 0.54);
      margin-right: 8px;
      margin-bottom: 4px;
      margin-left: 8px; }

    .MuiFormControl-root, .MuiInputBase-root {
      width: 290px; } }

  button {
    width: 100%;
    height: 60px;
    background: #CCF;
    margin-top: 20px; }
  button:hover {
    background: #BBF; } }
